<template>
  <div class="custom-edit-wrap">
    <el-row class="breadcrumb-style">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }">网站防护</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/protection/' + domain }">防护配置</el-breadcrumb-item>
        <el-breadcrumb-item :to="{ path: '/scan-attack-protection/' + domain }">网页防篡改</el-breadcrumb-item>
        <el-breadcrumb-item v-if="uuid == 'new'">新增</el-breadcrumb-item>
        <el-breadcrumb-item v-else>编辑</el-breadcrumb-item>
      </el-breadcrumb>
    </el-row>
    <el-row class="container-style">
      <el-col :span="24" v-loading.fullscreen.lock="loadingPage">
        <el-form class="custom-edit-form" :model="pageTamperProofForm" :rules="rules" ref="pageTamperProofForm"
          label-width="180px">
          <div>
            <el-form-item label="规则名称" prop="rule_name">
              <el-input v-if="uuid == 'new'" v-model="pageTamperProofForm.rule_name"
                placeholder="请输入字母开头，字母或数字组合，仅支持_-两种符号"> </el-input>
              <el-input v-else v-model="pageTamperProofForm.rule_name" disabled> </el-input>
            </el-form-item>
            <el-form-item label="规则详情">
              <el-input v-model="pageTamperProofForm.rule_detail"> </el-input>
            </el-form-item>

            <el-form-item label="规则匹配" class="is-required">
              <div class="match-box" v-for="(item, index) in ruleSmallMatchs" :key="index">
                <div class="match-box-content">
                  <div class="match_key_cascader">
                    <el-cascader separator=":" v-model="item.rule_match_key_list" :options="optionsMatchKey"
                      :props="propsMatchKey" @change="onChangeRuleMatchs($event, item)" clearable>
                    </el-cascader>
                  </div>
                  <div v-show="item.showInput" class="match_key_input">
                    <el-input v-model="item.rule_match_key" clearable
                      @change="onChangeRuleInput($event, item)"></el-input>
                  </div>
                </div>

                <el-button @click.prevent="removeRuleMatchs(item)">删除</el-button>
              </div>
              <el-button @click="addRuleMatchs()" plain type="primary" class="button-new">新增</el-button>
            </el-form-item>

            <el-form-item label="页面地址" class="is-required">
              <div class="match-box" style="width: 100%;">
                <div class="match-box-content" style="width: calc(100% - 160px);">
                  <el-input v-model="pageTamperProofForm.cache_page_url" placeholder="请输入完整地址，例如：https://www.jxwaf.com">
                  </el-input>
                </div>
                <el-button type="primary" plain @click="getPageUrl()" :loading="loading">获取页面内容</el-button>
              </div>
            </el-form-item>
            <el-form-item label="页面内容">
              <el-input v-model="pageTamperProofForm.cache_page_content" type="textarea" :rows="4"> </el-input>
            </el-form-item>
            <el-form-item label="内容类型">
              <el-input v-model="pageTamperProofForm.cache_content_type"> </el-input>
            </el-form-item>
          </div>
        </el-form>
        <el-row type="flex" class="margin-border" justify="space-between">
          <el-col :span="12">
            <a class="el-button el-button--primary is-plain" :href="'/#/page-tamper-proof/' + domain">返回</a>
          </el-col>
          <el-col :span="12" class="text-align-right">
            <el-button type="primary" @click="onClickWebRuleProSubmit('pageTamperProofForm')" :loading="loading">保存
            </el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>

  </div>
</template>
<script>
import { mixin, JXAjax, validateRuleName } from "../assets/scripts/common";
import { useRoute } from 'vue-router'
export default {
  mixins: [mixin],
  data() {
    return {
      loading: false,
      loadingPage: false,
      domain:'',
      uuid: 'new',
      pageTamperProofForm: {
        rule_detail: "",
      },
      type: 'edit',
      optionsMatchKey: [
        {
          value: 'http_args',
          label: 'http_args',
          children: [
            { value: 'path', label: 'path', leaf:true },
            { value: 'query_string', label: 'query_string', leaf:true },
            { value: 'method', label: 'method', leaf: true },
            { value: 'src_ip', label: 'src_ip', leaf: true },
            { value: 'raw_body', label: 'raw_body', leaf: true },
            { value: 'version', label: 'version', leaf: true },
            { value: 'scheme', label: 'scheme', leaf: true },
            { value: 'raw_header', label: 'raw_header', leaf: true },
          ]
        },
        {
          value: 'header_args',
          label: 'header_args',
          children: [
            { value: 'host', label: 'host', leaf: true },
            { value: 'cookie', label: 'cookie', leaf: true },
            { value: 'referer', label: 'referer', leaf: true },
            { value: 'user_agent', label: 'user_agent', leaf: true },
            { value: 'default', label: '自定义', leaf: true },
          ]
        },
        {
          value: 'cookie_args',
          label: 'cookie_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'uri_args',
          label: 'uri_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'post_args',
          label: 'post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
        {
          value: 'json_post_args',
          label: 'json_post_args',
          children: [
            { value: 'default', label: '自定义', leaf: true }
          ]
        },
      ],

      ruleSmallMatchs: [{
        rule_match_key_list: [],
        rule_match_key: '',
        showInput: false
      }],
      propsMatchKey: {
        expandTrigger: 'hover',
      },
    };
  },
  computed: {
    rules() {
      return {
        rule_name: [
          {
            required: true,
            message: '请输入规则名称',
            trigger: ["blur", "change"],
          },
          {
            validator: validateRuleName,
            trigger: ["blur", "change"],
          },
        ],
        
      }
    }
  },
  mounted() {
    var t = this;
    const route = useRoute()
    t.uuid = route.params.uuid;
    t.domain = route.params.domain;
    t.loadingPage = false
    if (t.uuid != "new") { 
      t.getData();
    }
  },
  methods: {
    getData() {
      var t = this;
      var postUrl = "/waf/waf_get_web_page_tamper_proof";
      var oData = { domain: t.domain, rule_name: t.uuid };
      JXAjax(
        "post",
        postUrl,
        oData,
        function (response) {
          t.loadingPage = false;
          t.pageTamperProofForm = response.data.message;
          t.pageTamperProofForm.rule_name = t.uuid;
          var _rule_matchs = JSON.parse(t.pageTamperProofForm.rule_matchs)
          var _match = [];
          var _default = [
            "query_string",
            "json_post",
            "post",
            "cookie",
            "waf_description",
            "ctx",
            "string"
          ]

          for (var j in _rule_matchs) {
            var item = _rule_matchs[j]
            var key = item.key
            var value = item.value
            var show = "false"
            if (_default.indexOf(key) > -1) {
              show = "true"
            }

            _match.push({
              "rule_match_key_list": [key, value],
              "rule_match_key": key + ':' + value,
              "showInput": show
            })
          }
          t.ruleSmallMatchs = _match;
         },
        function () {
          t.loadingPage = false;
        },
        "no-message"
      );
      
    },
    onClickWebRuleProSubmit(pageTamperProofForm) {
      var t = this;      
      var _match = [];
      if (t.ruleSmallMatchs.length == 0) {
        t.$message({
          message: '请选择统计对象',
          type: "error",
        });
        return false;
      }
      if (t.ruleSmallMatchs[0].rule_match_key == "") {
        t.$message({
          message: '请选择统计对象',
          type: "error",
        });
        return false;
      }
      

      for (var j in t.ruleSmallMatchs) {
        var item = t.ruleSmallMatchs[j]
        var _str = "";
        var _arr = [];
        var _key = "";
        var _value = "";

        if (item.rule_match_key) {
          _arr = item.rule_match_key.split(":");
        }
        if (_arr.length > 0) {
          _key = _arr[0];
          _value = item.rule_match_key.replace(new RegExp(_key + ":"), "");
          _str = '{"key":"' + _key + '" , "value":"' + _value + '"}'
        }
        _match.push(JSON.parse(_str))
      }
      
      t.pageTamperProofForm.domain = t.domain;
     
      var postUrl = "/waf/waf_edit_web_page_tamper_proof";

      if (t.uuid == 'new') {
        postUrl = "/waf/waf_create_web_page_tamper_proof";
      } else {
        t.pageTamperProofForm.rule_name = t.uuid;
      }
      t.pageTamperProofForm.rule_matchs = JSON.stringify(_match);
      this.$refs[pageTamperProofForm].validate((valid) => {
        if (valid) {
          t.loading = true;
          JXAjax(
            "post",
            postUrl,
            t.pageTamperProofForm,
            function (response) {
              t.loading = false;
              window.location.href = '/#/page-tamper-proof/' + t.domain;
              
            },
            function () {
              t.loading = false;
            }
          );
        }
      });
    },
    getPageUrl() { 
      var t = this;
      var url = t.pageTamperProofForm.cache_page_url;
      if (url) {
        t.loading = true;
        var oData = {
          cache_page_url: url,
        }
        var postUrl = "/waf/waf_get_cache_page_url";
        JXAjax(
          "post",
          postUrl,
          oData,
          function (response) {
            t.loading = false;
            t.pageTamperProofForm.cache_page_content = response.data.cache_page_content;
            t.pageTamperProofForm.cache_content_type = response.data.cache_content_type;
          },
          function () {
            t.loading = false;
          },
          "no-message"
        );
      } else { 
        t.$message({
          message: '请输入页面地址',
          type: "error",
        });
        return false;
      }
      
    },
    removeRuleMatchs(item) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if (index !== -1) {
        this.ruleSmallMatchs.splice(index, 1);
      }
    },
    addRuleMatchs() {
      this.ruleSmallMatchs.push({ rule_match_key: '', showInput: false })
    },
    onChangeRuleMatchs(event, item) {
      if (event && event.length > 0) {
        var index = this.ruleSmallMatchs.indexOf(item);
        if (event[1] == 'default') {
          this.ruleSmallMatchs[index].showInput = true;
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':';
        } else {
          this.ruleSmallMatchs[index].rule_match_key = event[0] + ':' + event[1];
        }
      }

    },
    onChangeRuleInput(event, item) {
      var index = this.ruleSmallMatchs.indexOf(item);
      if (event == '') {
        this.ruleSmallMatchs[index].showInput = false;
        this.ruleSmallMatchs[index].rule_match_key = "";
        this.ruleSmallMatchs[index].rule_match_key_list = [];
      } else {
        this.ruleSmallMatchs[index].rule_match_key = event;
      }
    },
  },
};
</script>
<style>
.custom-edit-wrap {
  max-width: 800px;
  min-width: 400px;
}
.custom-edit-wrap .match-inline-block {
  width: 192px;
}
.custom-edit-form .el-checkbox + .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-checkbox {
  margin-left: 0px;
  margin-right: 30px;
}
.custom-edit-form .el-select {
  width: 100%;
}
.custom-edit-form .match-box {
  
  display: inline-block;
  margin-bottom: 10px;
}
.custom-edit-form .el-button {
  margin-left: 10px;
}

.custom-edit-form .match-box-content {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_cascader {
  position: relative;
  display: inline-block;
}
.custom-edit-form .match_key_input {
  position: absolute;
  display: inline-block;
  top: 0;
  left: 0;
  width: 100%;
}
.custom-edit-form .rule-level-box .el-form-item__content {
  margin-left: 10px;
}
.box-card-rule {
  margin-bottom: 22px;
  border: 1px solid #409eff;
}
.box-card-rule .card-item {
  border-bottom: 1px solid #e0e3e9;
  margin-bottom: 20px;
}
.box-card-rule .card-footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.box-card-rule .card-item-bottom {
  text-align: right;
  margin-bottom: 20px;
}
</style>
